import Search from '@/components/header/header.vue';
import toRegister from '@/components/toRegister/toRegister.vue';
export default {
	data() {
		return {
			code: '',
			countdown: '获取验证码',
			disabled: false,
			timestatus: false,
			timestatus_two: true,
			clear: '',
			countown_style: {
				// 	width: '149upx',
				//zIndex: 2,
				// 	background: '#71D3BF',
				// 	border:''
				// 	// border: 1px solid rgba(168, 167, 167, 1);
			},
			//content: '获取验证码', // 按钮里显示的内容
			totalTime: 60 //记录具体倒计时时间 
		}
	},
	onLoad() {

	},
	methods: {
		// 获取input内容
		getCode() {
			this.countdown = 3;
			this.timestatus_two = false;
			this.timestatus = true;
			this.clear = setInterval(this.countDown, 1000);
			this.countown_style.zIndex = -1;
		},
		countDown() {
			if (!this.countdown) {
				this.disabled = false;
				this.timestatus = false;
				this.countdown = '获取验证码';
		
				clearInterval(this.clear);
				this.timestatus_two = true;
				this.countdown = '重新获取';
				this.countown_style.zIndex = 2;
			} else {
		
				--this.countdown;
			}
		},
		next() {
			uni.navigateTo({
				url: 'changePassword',
			});
		}
	},
	components: {
		Search,
		toRegister
	}
}
